/* components/Loading.css */
.loading-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.8); /* Nn m  ni bt nh loading */
    z-index: 9999; /* m bo loading hin th trn cng */
  }
  
  .loading-gif {
    width: 100px; /* Kch thc nh loading */
    height: 100px;
  }
  